import React from "react";
import { createRoot } from 'react-dom/client';
import moment from "moment";
import Row from 'react-bootstrap/Row';
import Table from 'react-bootstrap/Table';
import axios from "axios";

import domtoimage from 'dom-to-image-more';

function viewer(e, reason) {
  return (
    <div style={{ width: '400px' }} className="ql-container ql-snow">
      <div className="ql-editor" data-gramm="false" contentEditable={false} data-placeholder="Text to print">
        <h3 className="ql-align-center">{e['Formatted Date']}</h3>
        <h2 className="ql-align-center">{e['Name']}</h2>
        <h3 className="ql-align-center">{e['Role']}</h3>
        <br />
        <h3 className="ql-align-center">
          <span className="ql-font-monospace">{e['Formatted Start']} - {e['Formatted End']}</span>
        </h3>
        <br />
        <h3 className="ql-align-center">
          <span className="ql-font-monospace">{reason}</span>
        </h3>
        <h3 className="ql-align-center">
          <span className="ql-font-monospace">{e['Total Hours']} hours</span>
        </h3>
        <p className="ql-align-center">
          <br />
        </p>
        {/* <h3 className="ql-align-center">6 hours: 9:51 PM</h3> */}
        {/* <h3 className="ql-align-center">Needed Break By: 8:51 PM</h3> */}
      </div>
    </div>
  );
}

const viewerToImage = async (data, reason) => {
  try {
    // Create an offscreen stage and mount the JSX into it
    const stage = document.createElement('div');
    stage.style.position = 'fixed';
    stage.style.left = '-9999px';
    stage.style.top = '-9999px';
    stage.style.width = '400px';
    stage.style.background = 'white';
    stage.style.padding = '8px';
    stage.style.boxSizing = 'border-box';
    stage.style.whiteSpace = 'pre-wrap';
    stage.style.overflowWrap = 'anywhere';
    stage.style.wordBreak = 'break-word';
    document.body.appendChild(stage);

    const root = createRoot(stage);
    root.render(viewer(data, reason));

    // Give the browser a tick to paint
    await new Promise((r) => setTimeout(r, 0));

    const SCALE = 576 / 400;
    const dataUrl = await domtoimage.toPng(stage, {
      pixelRatio: SCALE,
      backgroundColor: '#ffffff'
    });
    // console.log(dataUrl)
    root.unmount();
    document.body.removeChild(stage);

    const base64 = dataUrl.split('base64,')[1];
    const body = JSON.stringify({ type: 'image', image_base64: base64, filename: 'editor.png' });
    return body;
  } catch (err) {
    console.error(err);
    return null;
  }
};

const handlePrint = async (event) => {
  try {
    const bodyToSend = await viewerToImage(event, 'No Break');
    // console.log('payload for printer:', bodyToSend?.slice(0, 120) + '...');
    // Example post (uncomment and adjust endpoint when ready):
    await axios.post('http://127.0.0.1:8000/0', JSON.parse(bodyToSend));
  } catch (error) {
    console.error('Error:', error);
  }
};

function NoBreak(props) {
    if (props.timeCards.length === 0) {
        return(
            <main>
                <h1>No Break</h1>
                <p>No break violations</p>
            </main>
        )
    }
    else {
        return (
            <main>
                <h1>No Break</h1>
                    <Row>
                    <Table striped bordered hover size="sm">
                        <thead>
                            <tr>
                            <th>Role</th>
                            <th>Name</th>
                            <th>Date</th>
                            <th>Total Hours</th>
                            <th>Break Hours</th>
                            <th>Start Time</th>
                            <th>End Time</th>
                            <th>6 Hours</th>
                            <th>Break Needed By</th>
                            </tr>
                        </thead>
                        <tbody>
                        {Object.keys(props.timeCards)?.map((desc, descKey) => {
                            if (props.timeCards[desc]["No Break"]) {
                                return(
                                    <tr key={descKey} >
                                    <td>{props.timeCards[desc]["Role"]}</td>
                                    <td><span onClick={() => handlePrint(props.timeCards[desc])} style={{cursor: 'pointer'}}>{props.timeCards[desc]["Name"]}</span></td>
                                    <td>{props.timeCards[desc]["Start Time"].format('L')}</td>
                                    <td>{props.timeCards[desc]["Total Hours"]}</td>
                                    <td>{props.timeCards[desc]["Break Hrs"]}</td>
                                    <td>{props.timeCards[desc]["Start Time"].format('LT')}</td>
                                    <td>{props.timeCards[desc]["End Time"].format('LT')}</td>
                                    <td>{moment(props.timeCards[desc]["Start Time"]).add(6, 'h').format('LT')}</td>
                                    <td>{moment(props.timeCards[desc]["Start Time"]).add(5, 'h').format('LT')}</td>
                                    </tr>
                                )
                            }
                            else {
                                return(<></>)
                            }
                        })}
                    </tbody>
                    </Table>
                </Row>
            </main>
        );
    }
}

export default NoBreak;